    text-align: center;
    padding-top: 50px;

    @media (max-width: 768px) {
      padding-top: 0;
    }

    .widgetContainer__body {
      display: flex;
      flex-direction: column;
    }

    .icon {
      font-size: 75px;
      margin-bottom: 36px;

      .path1 {
        &:before {
          color: var(--color-green);
        }
      }
    }

    .title,
    .description {
      padding-left: 40px;
      padding-right: 40px;
    }

    .title {
      font-size: 24px;
      line-height: 30px;
      padding-bottom: 16px;
    }

    .description {
      color: rgba(#3c3c43, 0.6);
      font-size: 14px;
      line-height: 20px;
    }

    .el-button {
      margin-top: auto;
    }
  }

  .declined {
    .icon {
      .path1 {
        &:before {
          color: var(--color-red);
        }
      }
    }
  }

  .verifying {
    .icon {
      .path1 {
        &:before {
          color: var(--orange2);
        }
      }
    }
  }

  .review {
    .icon {
      .path1 {
        &:before {
          color: var(--orange2);
        }
      }
    }
